import React, { Suspense } from "react";
import { Routes, Route, Navigate } from "react-router-dom";

import "./App.css";

import Nav from "./components/Nav";
import Loading from "./components/Loading";

const DailyPosts = React.lazy(() => import("./DailyPosts"));
const MonthlyPosts = React.lazy(() => import("./MonthlyPosts"));

export default function App() {
  return (
    <div className="layout index main">
      <div className="content-comp side-menu">
        <div className="content-inner">
          <div className="nav-area">
            <Nav />
          </div>
          <div className="content-area">
            <Suspense fallback={<Loading />}>
              <Routes>
                <Route path="/feeds/daily" element={<DailyPosts />} />
                <Route path="/feeds/monthly" element={<MonthlyPosts />} />
                <Route
                  path="*"
                  element={<Navigate to="/feeds/daily" replace />}
                />
              </Routes>
            </Suspense>
          </div>
        </div>
      </div>
    </div>
  );
}
